import React from "react";
import styled from "styled-components";
import { Link } from "gatsby";
import { respondTo } from "./shared/_respondTo";

const BlogListContainer = styled.div`
  width: 1024px;
  max-width: 1024px;
  min-height: 600px;
  ${respondTo.md`
    width: 100%;
    max-width:100%;
  `}
`;

const BlogListContent = styled.div`
  display: flex;
  flex-direction: column;
`;

const BlogPost = styled.div`
  display: flex;
  justify-content: space-between;
  ${respondTo.md` 
    flex-direction: column;
    max-width:100%;
    padding:0.5rem 0;
  `}
`;

const BlogTitle = styled.span``;

const BlogDate = styled.span``;

const BlogPagination = styled.div`
  margin: 8px 0px;
`;

const PageNavLink = styled(Link)`
  margin: 0px 8px;
  :first-child {
    margin: 0;
    margin-right: 8px;
  }

  :last-child {
    margin: 0;
    margin-left: 8px;
  }
`;

export default function BlogListComponent(props) {
  const pagination = props.pagination;

  return (
    <BlogListContainer>
      <BlogListContent>
        {props.posts.map(({ node }) => {
          const title = node.frontmatter.title || node.fields.slug;
          return (
            <Link key={node.fields.slug} to={node.frontmatter.path}>
              <BlogPost>
                <BlogTitle>{`${title}`}</BlogTitle>
                <BlogDate>{node.frontmatter.date.split(" ")[0]}</BlogDate>
              </BlogPost>
            </Link>
          );
        })}
      </BlogListContent>
      <BlogPagination>
        {pagination
          ? pagination.numPages > 1
            ? Array(pagination.numPages)
                .fill(0)
                .map((_, idx) => (
                  <PageNavLink
                    key={idx + 1}
                    to={idx === 0 ? `/blog` : `/blog/${idx + 1}`}
                  >
                    {idx + 1}
                  </PageNavLink>
                ))
            : null
          : null}
      </BlogPagination>
    </BlogListContainer>
  );
}
